import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController,ViewController } from 'ionic-angular';
import { AppSoundProvider } from '../../providers/app-sound/app-sound';

@IonicPage()
@Component({
  selector: 'page-play-games-popups',
  templateUrl: 'play-games-popups.html',
})
export class PlayGamesPopupsPage {

  public howToPlayModal: any;
  private gameInfo: any[];

  constructor(public navCtrl: NavController, private viewCtrl: ViewController,  private navParms: NavParams, public navParams: NavParams,private appSound: AppSoundProvider, private _modalController: ModalController) {
   
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PlayGamesPopupsPage');
  }
  howToPlay() {
    this.viewCtrl.dismiss();
    this.appSound.play('buttonClick');
    this.howToPlayModal = this._modalController.create('howtoplay', { gameInfo: this.navParms.get("GameInfo")})
    this.howToPlayModal.present();
  }
  popCloseModel()
  {
    this.viewCtrl.dismiss();
  }
  recentWinnerTipsmodal() {
    this.viewCtrl.dismiss();
    this.appSound.play('buttonClick');
    this.howToPlayModal = this._modalController.create('recentWinnerTips', { gameInfo: this.navParms.get("GameInfo")})
    this.howToPlayModal.present();
  }
}



